.main{
    border: 2px solid black;
    border-radius: 5%;
    background-color: blueviolet;
    height: 350px;
    width: 200px;
    position: relative;
    left: 550px;
    top:50px;
    display: block;
    visibility: visible;
    
}
.display{
    position: relative;
    width:160px;
    height: 100px;
    border: 2px dotted black;
    margin: 20px;
    background-color: rgb(9, 116, 116);
    background-color: black;
    
    
}
.temp button{
    width: 50px;
    height: 30px;
    font-size: 30px;
    background-color: brown;
    color: aliceblue;
    display: flex;
    justify-content: center;  
    
}
.temp{
    width: 50px;
    height: 120px;
    border: 1px solid black;
    padding: 5px;
    position: relative;
    left: 110px;
    background-color: aqua;
    
}
.swing{
    padding: 20px;
    padding-top: 10px;

}
.swing button{
    width: 80px;
    height: 45px;
    background-color: aqua;



}
.mode{
    position: absolute;
    padding-left: 20PX;
    
    
}
.mode button{
    width: 70px;
    height: 45px;
    background-color: aqua;
}

.powerOff{
    position: relative;
    width:160px;
    height: 100px;
    border: 2px dotted black;
    margin: 20px;
    background-color: rgb(3, 3, 3);
    display: none;
    
}
.display h4{
    height: 10px;
    position: absolute;
    top: 0px;
    left: 85px;
}
